import axios from 'axios';
import React, { useEffect, useState } from 'react';


function convertDate(dateString) {
    const [year, month, day] = dateString.split('-');
    const reversedDate = [day, month, year].join('/');
    return reversedDate;
}



const CardFav = ({ f, removeFavorite }) => {
    let [genres, setGenre] = useState([]);
    const fetchGenre = () => {
        axios.get('https://api.themoviedb.org/3/genre/movie/list?api_key=9cc08874864e905acb9c23a314488e75').then((reponse) => {
            setGenre(reponse.data.genres);
        }
        );
    }
    useEffect(fetchGenre, []);
    function getGenreName(genreId) {
        const genre = genres.find(g => g.id === genreId);
        return genre ? genre.name : "";
    }
    const GenreList = () => {
        const genreElements = [];

        for (let i = 0; i < f.genre_ids.length; i++) {
            genreElements.push(<li key={getGenreName(f.genre_ids[i])}>{getGenreName(f.genre_ids[i])}</li>);
        }

        return <div className='genre'>{genreElements}</div>;
    };
    return (
        <div className="card">
            <div className="entete">
                <span><h2>{f.original_title}</h2></span>
                <span> {Number((f.vote_average).toFixed(2))} /10</span>
            </div>
            <div className="img-container">
                <img src={f.poster_path == null ? 'https://media.discordapp.net/attachments/1018865262709190819/1091264239110455356/affiche.webp?width=615&height=615' : `https://image.tmdb.org/t/p/w300/${f.poster_path}`} alt="" />
            </div>
            <div className="info-container">
                <span>{convertDate(f.release_date)}</span>
                <GenreList />
                <span>{f.overview}</span>


            </div>
            <button onClick={() => removeFavorite(f.id)}>Remove Favorite</button>
        </div>
    )


};

export default CardFav;