import axios from 'axios';
import React, { useEffect, useState } from 'react';


function convertDate(dateString) {
    const [year, month, day] = dateString.split('-');
    const reversedDate = [day, month, year].join('/');
    return reversedDate;
}



const Card = ({ movie }) => {
    let [genres, setGenre] = useState([]);
    const [isFavorite, setIsFavorite] = useState(false);

    const fetchGenre = () => {
        axios.get('https://api.themoviedb.org/3/genre/movie/list?api_key=9cc08874864e905acb9c23a314488e75').then((reponse) => {
            setGenre(reponse.data.genres);
        }
        );
    }
    // Ajoute le film aux favoris
    const addFavorite = () => {
        const favorites = JSON.parse(localStorage.getItem('favorites')) || [];
        if (!favorites.some((fav) => fav.id === movie.id)) {
            favorites.push({
                id: movie.id,
                original_title: movie.original_title,
                vote_average: movie.vote_average,
                poster_path: movie.poster_path,
                release_date: movie.release_date,
                overview: movie.overview,
                genre_ids: movie.genre_ids
            });
            localStorage.setItem('favorites', JSON.stringify(favorites));
            setIsFavorite(true);
        }
    };
    // Supprime le film des favoris
    const removeFavorite = () => {
        const favorites = JSON.parse(localStorage.getItem('favorites')) || [];
        const updatedFavorites = favorites.filter((fav) => fav.id !== movie.id);
        localStorage.setItem('favorites', JSON.stringify(updatedFavorites));
        setIsFavorite(false);
    };

    useEffect(() => {
        const favorites = JSON.parse(localStorage.getItem('favorites')) || [];
        setIsFavorite(favorites.some((fav) => fav.id === movie.id));
    }, [movie.id]);

    useEffect(fetchGenre, []);
    function getGenreName(genreId) {
        const genre = genres.find(g => g.id === genreId);
        return genre ? genre.name : "";
    }
    const GenreList = () => {
        const genreElements = [];

        for (let i = 0; i < movie.genre_ids.length; i++) {
            genreElements.push(<li key={getGenreName(movie.genre_ids[i])}>{getGenreName(movie.genre_ids[i])}</li>);
        }

        return <div className='genre'>{genreElements}</div>;
    };
    return (


        <div className={`card ${isFavorite ? 'favorite' : ''}`}>
            <div className="entete">
                <span><h2>{movie.original_title}</h2></span>
                <span> {Number((movie.vote_average).toFixed(2))} /10</span>

            </div>
            {isFavorite ? (
                <button className="btn btn-danger" onClick={removeFavorite}>
                    Remove from favorites
                </button>
            ) : (
                <button className="btn btn-primary" onClick={addFavorite}>
                    Add to favorites
                </button>
            )}
            <div className="img-container">

                <img src={movie.poster_path == null ? 'https://media.discordapp.net/attachments/1018865262709190819/1091264239110455356/affiche.webp?width=615&height=615' : `https://image.tmdb.org/t/p/w300/${movie.poster_path}`} alt="" />
            </div>
            <div className="info-container">
                <span>{convertDate(movie.release_date)}</span>
                <GenreList />
                <span>{movie.overview}</span>


            </div>
        </div>

    );
};

export default Card;