import React, { useEffect, useState } from 'react';
import axios from 'axios';
import Card from '../components/Card';
import { Link } from 'react-router-dom'
import StarIcon from '@mui/icons-material/Star';


const HomePage = () => {

    let [movies, setMovie] = useState([]);
    let [searchInput, setSearchInput] = useState('e');
    const [favorites, setFavorites] = useState([]);
    const fetchCountries = () => {
        axios.get('https://api.themoviedb.org/3/search/movie?api_key=9cc08874864e905acb9c23a314488e75&language=fr-FR&query=' + searchInput).then((reponse) => {
            setMovie(reponse.data.results);
        }
        );

    }

    useEffect(fetchCountries, [searchInput]);

    return (
        <div className="app">

            <header>

                <h1>React Movie</h1>
                <Link to="/favorites"><StarIcon fontSize="large" color="primary" /></Link>
                <input type="text" id='searchInput' onChange={(e) => setSearchInput(e.target.value)} placeholder="Tapez le nom du film" />

            </header>
            <section className="flags-container" >

                {movies && movies.map((movie, index) => <Card key={index} movie={movie} favorites={favorites} setFavorites={setFavorites} />)}
                {/* {movies.map(movie => (<Movie key={movie.id} movie={movie} favorites={favorites} setFavorites={setFavorites} />))} */}
            </section>
        </div>



    );
};

export default HomePage;